*,
*::after,
*::before {
  box-sizing: border-box;
}
p {
  margin: 0;
}
ul,
ol {
  list-style-type: none;
}
.page {
  margin: 0;
}
.page__wrapper {
  width: 100%;
  min-height: 696px;
  margin-top: 60px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 4px 26px 0 #e1e1e1;
}
@media (min-width: 1300px) {
  .page__wrapper {
    max-width: 1300px;
    margin-top: 114px;
    margin-bottom: 139px;
  }
}
.header__first-block {
  min-height: 44px;
  background-color: #005FAE;
}
.header__second-block {
  min-height: 44px;
  background: #F7F7F7;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
}
.main {
  display: flex;
  justify-content: space-between;
  max-width: 1366px;
  padding-top: 32px;
  padding-left: 33px;
  padding-right: 33px;
}
.content {
  display: flex;
  flex-direction: column;
}
.sidebar {
  display: flex;
  flex-direction: column;
}
.sidebar__banner {
  width: 160px;
  height: 320px;
  background: #FAFAFA;
}
.company-header {
  display: flex;
  flex-direction: column;
}
.breadcrumbs {
  color: red;
}
